<template>
    <div>
        <head-top webActive="mine"></head-top>
        <div class="bgGray" id="unIndex">
            <div class="container">
                <div class="ucBg">
                    <side-bar></side-bar>
                    <div class="ucPanel ucP_Box">
                        <div class="ucGreenTit">消息通知</div>
                        <div v-if="list.length==0" class="tc mt30 f16">
                            暂无消息
                        </div>
                        <div v-else>
                            <el-collapse accordion  class="mlist">
                                <el-collapse-item v-for="(item,index) in list" :key="index">
                                    <template slot="title">
                                        <div class="ucP_m_tit" @click="isSee(item.id,index)">
                                            <i :class="item.is_see==0?'el-icon-message colorGreen f16 mr10':'el-icon-message color9 f16 mr10'"></i>
                                            {{item.title}}
                                        </div>
                                        <span class="color9 tr w mr10">{{item.send_time}}</span>
                                    </template>
                                    <div>{{item.message}}</div>
                                </el-collapse-item>
                            </el-collapse>
                            <el-pagination
                                    class="tc mt15"
                                    @current-change="handleCurrentChange"
                                    hide-on-single-page
                                    background
                                    layout="prev, pager, next"
                                    :current-page="page"
                                    :page-size="rows"
                                    :total="count">
                            </el-pagination>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {message,isSee} from '../../../service/api'
    import sideBar from '../../../components/usercenter/sideBar';
    import headTop from '../../../components/header/headTop';
    export default {
        name: 'message',
        data(){
            return{
                list:[],
                rows:8,//每页显示数
                page:1,//当前页面
                count:0//总条数
            }
        },
        components:{
            sideBar,
            headTop
        },
        mounted(){
            this.getUserInfo();
            this.initData();

        },
        computed:{
            ...mapState([
                'userInfo','login'
            ]),

        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            async initData(){
                var res = await message(
                    this.rows,
                    this.page
                );
                if(res){
                    if(res.status != 12007){
                        this.list = res.data;
                        this.count = res.count;
                    }else{
                        this.$message({
                            message: res.msg,
                            type: 'warning'
                        });
                    }
                }
                console.log(res);
            },
            toLogin(){
                this.$router.push({path:"/login/login"});
            },
            async isSee(id,index){
                var res = await isSee(
                    id,
                    1
                );
                if(res.status == 12000){
                    this.list[index].is_see = 1;
                }
            },
            handleCurrentChange(val) {
                //改变页码
                this.page = val;
                this.initData();
            }
        },
        created(){

        },

    }

</script>

<style scoped>
    /*我的消息*/
    .mlist{min-height: 400px;border:none;}
    .ucP_m_tit{
        width:800px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        flex-shrink:0;
        font-size: 14px;
    }

</style>



